export default {
	props: {
		searchStr: {
			type: String,
			default: ''
		},
		placeholderStr: {
			type: String,
			default: ''
		},
		type: { // 1: 销售发货单
			type: String,
			default: '1'
		}
	},
	data() {
		return {
			selIndex: 0
		}
	},
	onLoad() {
	},
	methods: {
		radioChange(val, index) {
			this.selIndex = index;
			this.$emit('radioChange', val);
		},
		okRadioValue () {
			this.$emit('okRadioValue');	
		},
		scanCode () { // 二维码识别
			this.$emit('scanCode');
		}
	}
	
}
